'use strict';
import {EventTarget} from "../common/event";
import {ICommandState} from "../common/iCommandState";
import {ICommandsProvider} from "../interfaces/iCommandsProvider";
import {SynchronousJsonPortMarshaler} from "./marshaler";
import {Promise} from "q";

export class CommandsMarshaler extends SynchronousJsonPortMarshaler {
    constructor(control: EventTarget, public menuAliases: string[], public commandAliases: string[], public commandsProvider: ICommandsProvider) {
        super(control);
    }

    public setCommandsStates(states: ICommandState[]): Promise<void> {
        return this.commandsProvider.setCommandsStates(states);
    }

    public showContextMenu(menuName: string, xPosition: number, yPosition: number): Promise<void> {
        return this.commandsProvider.showContextMenu(menuName, xPosition, yPosition);
    }

    public fireCommandExecuted(commandName: string): void {
        this.fireEvent('commandexec', {
            CommandName: commandName
        });
    }

    public initializeState(): void {
        this.fireEvent('commandsinitialized', {
            menuAliases: this.menuAliases,
            commandAliases: this.commandAliases,
        });
    }
}